import React from 'react';
import { StaticQuery } from 'gatsby';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPhone, faEnvelope, faMapMarkerAlt } from '@fortawesome/free-solid-svg-icons';
import { servicesQuery } from './Services';
import { menuItemsQuery } from './Header';
import { profileQuery } from './Hero';
import SocialIcons from './SocialIcons';

const Footer = () => (
    <footer className="footer">
        <section id="footer">
            <div className="container">
                <div className="row">
                    <div className="col-md-3 col-sm-12">
                        {/* TODO */}
                        <h4>Name</h4>
                        <p className="description">
                            Lorem ipsum dolor sit amet, consectetur adipisicing elit. Velit ducimus voluptatibus neque
                            illo id repellat quisquam? Autem expedita earum quae laborum ipsum ad.
                        </p>
                        <SocialIcons />
                    </div>
                    <div className="col-md-3 col-sm-12">
                        <h4>What We Do</h4>
                        <StaticQuery
                            query={servicesQuery}
                            render={({
                                site: {
                                    siteMetadata: { services },
                                },
                            }) => (
                                <ul>
                                    {services.map(service => (
                                        <li key={service.title}>{service.title}</li>
                                    ))}
                                </ul>
                            )}
                        />
                    </div>
                    <div className="col-md-3 col-sm-12">
                        <h4>Navigate</h4>
                        <StaticQuery
                            query={menuItemsQuery}
                            render={({
                                site: {
                                    siteMetadata: {
                                        navigation: { menu, cta },
                                    },
                                },
                            }) => (
                                <ul>
                                    {menu.map(({ title, url }) => (
                                        <li key={title}>
                                            <a href={url}>{title}</a>
                                        </li>
                                    ))}
                                    <li key={cta.title}>
                                        <a href={cta.url}>{cta.title}</a>
                                    </li>
                                </ul>
                            )}
                        />
                    </div>
                    <div className="col-md-3 col-sm-12">
                        <h4>Contact us</h4>
                        <StaticQuery
                            query={profileQuery}
                            render={({
                                site: {
                                    siteMetadata: { profile },
                                },
                            }) => (
                                <>
                                    <div className="row contact-row">
                                        <div className="col-sm-12">
                                            <FontAwesomeIcon icon={faPhone} className="contact-icons" />{' '}
                                            <strong>Phone</strong>
                                        </div>
                                        <div className="col-sm-12">
                                            <a href={`tel:${profile.contact.phone}`}>{profile.contact.phone}</a>
                                        </div>
                                    </div>
                                    <div className="row contact-row">
                                        <div className="col-sm-12">
                                            <FontAwesomeIcon icon={faEnvelope} className="contact-icons" />{' '}
                                            <strong>Email</strong>
                                        </div>
                                        <div className="col-sm-12">
                                            <a href={`mailto:${profile.contact.email}`}>{profile.contact.email}</a>
                                        </div>
                                    </div>
                                    <div className="row contact-row">
                                        <div className="col-sm-12">
                                            <FontAwesomeIcon icon={faMapMarkerAlt} className="contact-icons" />{' '}
                                            <strong>Address</strong>
                                        </div>
                                        <div className="col-sm-12">{profile.office.address}</div>
                                    </div>
                                </>
                            )}
                        />
                    </div>
                </div>
                <div className="copyright row">
                    <div className="col-sm-12"> TODO {new Date().getFullYear()}</div>
                </div>
            </div>
        </section>
    </footer>
);

export default Footer;
