import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPhone, faEnvelope, faMapMarkerAlt } from '@fortawesome/free-solid-svg-icons';
import SocialIcons from './SocialIcons';
import avatar from './../../static/lawyer_profile.jpg';

export const profileQuery = graphql`
    query HeroComponentQuery {
        site {
            siteMetadata {
                title
                profile {
                    name
                    title
                    description
                    contact {
                        phone
                        email
                    }
                    office {
                        address
                    }
                }
            }
        }
    }
`;

const Hero = () => {
    return (
        <StaticQuery
            query={profileQuery}
            render={({
                site: {
                    siteMetadata: { profile },
                },
            }) => (
                <section id="home" className="hero">
                    <div className="container">
                        <div className="row">
                            <div className="col-sm-12 col-md-4">
                                <img src={avatar} className="avatar" alt={profile.name} />
                            </div>
                            <div className="col-sm-12 col-md-8">
                                <div className="row">
                                    <div className="col-sm-12">
                                        <h1>{profile.name}</h1>
                                        <h3>{profile.title}</h3>
                                        <SocialIcons size="2x" />
                                        <p className="description">{profile.description}</p>
                                    </div>
                                </div>
                                <div>
                                    <div className="row contact-row">
                                        <div className="col-sm-3">
                                            <FontAwesomeIcon icon={faPhone} className="contact-icons" />{' '}
                                            <strong>Phone</strong>
                                        </div>
                                        <div className="col-sm-9">
                                            <a href={`tel:${profile.contact.phone}`}>{profile.contact.phone}</a>
                                        </div>
                                    </div>
                                    <div className="row contact-row">
                                        <div className="col-sm-3">
                                            <FontAwesomeIcon icon={faEnvelope} className="contact-icons" />{' '}
                                            <strong>Email</strong>
                                        </div>
                                        <div className="col-sm-9">
                                            <a href={`mailto:${profile.contact.email}`}>{profile.contact.email}</a>
                                        </div>
                                    </div>
                                    <div className="row contact-row">
                                        <div className="col-sm-3">
                                            <FontAwesomeIcon icon={faMapMarkerAlt} className="contact-icons" />{' '}
                                            <strong>Address</strong>
                                        </div>
                                        <div className="col-sm-9">{profile.office.address}</div>
                                    </div>
                                    <div className="row">
                                        <div className="col-12">
                                            <a className="btn btn-primary hero-cta" href="#contact" role="button">
                                                Contact me
                                            </a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            )}
        />
    );
};
export default Hero;
